.PhoneBookForm{
    display: flex;
    flex-direction: column;
    width: 400px;
    margin: 0 auto;
    background-color: #888888;
    color: #f8f8f8;
    padding: 40px;
    border-radius: 10px;
    &>label:last-child{
        margin-top: 20px;
    }
    &>input{
        width: 100%;
        display: block;
        padding: 10px;
        border-radius: 5px;
        transition: all 250ms linear;
        border: 1px solid transparent;
        color: #000;
        background-color: #f8f8f8;
        &:hover, &:focus, &:focus-visible{
            outline: none;
            border: 1px solid blue;
        }
    }
    &>button{
        display: block;
        min-width: 100px;
        margin: 0 auto;
        background-color: blue;
        color: #f8f8f8;
        border-radius: 5px;
        padding: 12px;
        border: none;
        cursor: pointer;
        margin-top: 20px;
        transition: all 250ms linear;
        &:hover{
            opacity: 0.8;
        }
    }
}
.Filter{
    width: 300px;
    margin: 0 auto;
    display: block;
    padding: 10px;
    border-radius: 5px;
    transition: all 250ms linear;
    border: 1px solid #888888;
    color: #000;
    background-color: #f8f8f8;
    &:hover, &:focus, &:focus-visible{
        outline: none;
        border: 1px solid blue;
    }
}
.Contacts{
    list-style: none;
    &>li{
        text-align: center;
    }
}
.ContactsName{
    display: inline-block;
    color: green
}
.ContactsNumber{
    display: inline-block;
    margin-left: 20px;
}
.DeleteBtn{
    display: inline-block;
    min-width: 50px;
    margin: 0 auto;
    background-color: blue;
    color: #f8f8f8;
    border-radius: 5px;
    padding: 6px;
    border: none;
    cursor: pointer;
    margin-left: 20px;
    transition: all 250ms linear;
    &:hover{
        opacity: 0.8;
    }
}
.Empty{
    text-align: center;
}